.posts{
  margin-top: 20px;
}

.newPost{
  border: 1px solid black;
  border-radius: 10px;
  padding: 10px;
}
.textarea{
  width: 100%;
  height: 70px;
  resize: none;
  padding: 10px;
}
.button{
  cursor: pointer;
  display: block;
  width: 100px;
  margin-left: auto;
  height: 30px;
  background: blue;
  color: white;
  border: none;
  border-radius: 10px;
}
