.post{
  position: relative;
  padding: 20px;
  border: 1px solid gray;
  border-radius: 10px;
  display: flex;
  align-items: center;
  margin: 10px 0;
  
}
.post img{
  border-radius: 50%;  
  width: 50px;
  height: 50px;
  margin-right: 25px;
}
.like, .dislike{
  position: absolute;
  cursor: pointer;
  width: 70px;
  height: 20px;
  border-radius: 10px;
  border: none;
  outline: none;
}
.like{
  bottom: 10px;
  right: 90px;
  color: #ffffff;
  background: green;
}
.dislike{
  bottom: 10px;
  right: 10px;
  color: #ffffff;
  background: red;
}